import { MultiSelect, Option } from 'react-rainbow-components';
import FadeIn from 'react-fade-in';

import youTubeVideosData from './youtube-videos.json';
import YoutubeVideo from './../components/YoutubeVideo';
/*
 * å–å¾—æ‰€æœ‰å½±ç‰‡çš„æŸé …è³‡è¨Š
 * key: å½±ç‰‡è³‡è¨Šæ¬„ä½
 * duplicate: é‡ä¸é‡è¤‡
 * chunk: é…åˆ BS Grid System é€²è¡Œè³‡æ–™åˆ†çµ„
 * (chunk=false) è¼¸å‡ºï¼š[a, b, c, ...]
 * (chunk=true) è¼¸å‡ºï¼š[[a, b, c], [...], ...]
 */
export const getItems = (key, duplicate=false, chunk = false) => {
	// æ”¶é›†æ‰€æœ‰è³‡æ–™
	let items = youTubeVideosData.reduce((arr, ele) => 
		arr.concat([ele[key]])
	, []);
	// å–ä¸é‡è¤‡
	if (!duplicate) {
		items = [...(new Set(items))];
	}
	// è³‡æ–™åˆ†çµ„
	if (chunk) {
		items = arrayToChunks(items, 6);
	}
	return items;
}
/*
 * é™£åˆ—å…ƒç´ åˆ†çµ„
 * arr: ä¸€éšé™£åˆ—
 * size: æ¯çµ„å…ƒç´ å€‹æ•¸
 * è¼¸å‡ºï¼šäºŒéšé™£åˆ—
 */
const arrayToChunks = (arr, size) => {
	var chunkedArray = [];
	while (arr.length) {
		chunkedArray.push(arr.splice(0, size));
	}
	return chunkedArray;
}
/*
 * ç”¢ç”Ÿ React Rainbow Components é¸å–®çš„é¸é …
 * items: ä¸€éšé™£åˆ—è³‡æ–™
 * è¼¸å‡ºï¼šç”¢ç”Ÿé¸é …æ‰€éœ€è³‡æ–™
 */
export const itemsToRainbowSelectOptions = (items) => {
	let allOption = { value: "*å…¨éƒ¨", label: "*å…¨éƒ¨" }; // é è¨­å€¼
	return items.reduce((arr, ele) => 
		arr.concat([{ value: ele, label: ele }])
	, [allOption]);
}
/*
 * ç”¢ç”Ÿ React Rainbow Components å¤šé¸é¸å–®çš„é¸é …
 * items: ä¸€éšé™£åˆ—è³‡æ–™
 * è¼¸å‡ºï¼šç”¢ç”Ÿå¤šé¸é¸é …
 */
export const itemsToRainbowMultiSelectOptions = (items, id, label, value, onChange, placeholder) => {
	return (
		<MultiSelect
			id={id}
			label={label}
			labelAlignment="left"
			hideLabel={false}
			isLoading={false} // é¸é …è¼‰å…¥ä¸­åœ–ç¤º
			value={value}
			onChange={onChange}
			placeholder={placeholder}
			variant="default" // ç¯©é¸æ¡† styleï¼›default, chip, bare
			chipVariant="brand" // é¸æ“‡çµæœ styleï¼›base, neutral, outline-brand, brand
			showCheckbox
		>
			{items.map((item, iIdx) => (
				<Option key={`ytMultiSelect-${iIdx}`} name={`option-${iIdx}`} label={item} value={item} />
			))}
		</MultiSelect>
	);
}
/*
 * å»ºç«‹æ‰€æœ‰å½±ç‰‡å¡ç‰‡
 * vids: æ‰€æœ‰å½±ç‰‡ ID
 * dates: æ‰€æœ‰å½±ç‰‡æ—¥æœŸ
 * locations: æ‰€æœ‰å½±ç‰‡åœ°é»
 * birds: æ‰€æœ‰å½±ç‰‡é³¥ç¨®
 * è¼¸å‡ºï¼šResponsive YouTube Video Cards
 */
export const vidsToVideoCards = (vids, dates, locations, birds, handleVideoPlay, handleVideoStop) => { // æš«æ™‚ birds/datesï¼Œå¯æ”¹å…¶ä»–åš header
	return (
		<FadeIn className="d-flex flex-row flex-wrap justify-content-center py-2" childClassName="ytVideoContainer m-2" delay="77" transitionDuration="777" wrapperTag="div" childTag="div" visible={true}>
		{/* æ‰€æœ‰å½±ç‰‡ç‚ºäº†ç™¾è‘‰çª—å‹•ç•«æ”¾åœ¨ä¸€èµ· */}
		{vids.map((vid, vIdx) => {
			let date = dates[vIdx];
			let location = locations[vIdx];
			let bird = birds[vIdx];
			return (
				<div key={`ytVideo-${vIdx}`} className={`${date} ${location} ${bird}`}>
					<div className="card">
						<div className="card-header"> {/* å½±ç‰‡æ¨™é¡Œ */}
							{date + " / " + location + " / " + bird}
						</div>
						<div className="card-body p-0"> {/* å½±ç‰‡å…§å®¹ */}
							<YoutubeVideo videoId={vid} videoLid="" handleVideoPlay={handleVideoPlay} handleVideoStop={handleVideoStop} />
						</div>
					</div>
				</div>
			);
		})}
		</FadeIn>
	);
}
