export const eBirderInfosToRainbowAvatars = (handleAvatarIndexChange) => {
	return (
		<div id="ebAvatars" className="text-center">
			{infos.map((info, iIdx) => { // å°æ–¼æ¯ä½ eBirder
				return (
					<Avatar
						key={`ebAvatar-${iIdx}`}
						className="eBirder"
						name={info['name']}
						value={iIdx.toString()}
						alt={info['name']}
						title={info['name']}
						size="100%" // åœ–åƒå¤§å°
						round={true} // border-radiusï¼›number, true(100%), false(0%)
						src={info['avatar']} // è¼‰å…¥ç…§ç‰‡
						style={{padding: "10px"}}
						onClick={() => {handleAvatarIndexChange(iIdx);}}
					/>
				);
			})}
		</div>
	);
}
