import { useState, useEffect } from 'react';

import { DatePicker } from 'react-rainbow-components';

import { getItems, itemsToRainbowMultiSelectOptions, vidsToVideoCards } from './../utils/ytVideos_dataExtraction';
import ScrollTopArrow from './../components/ScrollTopArrow';
import './YouTube.css';
// ç¯©é¸åƒæ•¸ç¯©é¸å€¼
var userParameters = {
	userDates: [],
	userLocations: [],
	userBirds: []
}
// Rainbow DatePicker çš„è¼¸å‡ºç°¡åŒ–ç‚ºå€¼é™£åˆ—
const datePickerOutputToValues = datePickerOutput => {
	return datePickerOutput.reduce((arr, ele) => {
		let localeDateStrings = ele.toLocaleDateString('zh-tw').split('/'); // 2021/7/20
		let year = localeDateStrings[0]; // 2021
		let month = localeDateStrings[1].padStart(2, '0'); // 07
		let day = localeDateStrings[2].padStart(2, '0'); // 20
		return arr.concat([`${year}-${month}-${day}`]); // 2021-07-20
	}, []);
}
// Rainbow MultiSelect çš„è¼¸å‡ºç°¡åŒ–ç‚ºå€¼é™£åˆ—
const multiSelectOutputToValues = multiSelectOutput => {
	return multiSelectOutput.reduce((arr, ele) => {
		return arr.concat([ele.value]);
	}, []);
}
// æ²å‹•é é¢ç«‹å³æš«åœæ‰€æœ‰æ’­æ”¾ä¸­çš„å½±ç‰‡
const pauseVideos = playingVideos => {
	for (let i = 0; i < playingVideos.length; i++) {
		playingVideos[i].pauseVideo();
	}
}
// æ”¶é›†æ’­æ”¾ä¸­çš„å½±ç‰‡
var playingVideos = []; // æ’­æ”¾ä¸­çš„å½±ç‰‡æ¸…å–®
const handleVideoPlay = target => { // ç•¶å½±ç‰‡æ’­æ”¾
	playingVideos.push(target); // åŠ å…¥æ’­æ”¾ä¸­å½±ç‰‡
}
const handleVideoStop = target => { // ç•¶å½±ç‰‡æš«åœæˆ–çµæŸ
	let index = playingVideos.indexOf(target);
	playingVideos.splice(index, 1); // å¾æ’­æ”¾ä¸­å½±ç‰‡æ¸…å–®ç§»é™¤
}

const YouTube = ({scrollToElement}) => {
	// è‹¥ç©éæ­¤é é¢å¾Œï¼Œåˆ‡æ›è‡³å…¶ä»–åˆ†é å†åˆ‡å›ä¾†ï¼Œç¯©é¸å€¼æ­¸é›¶
	const setUserParameters = () => {
		userParameters = {
			userDates: [],
			userLocations: [],
			userBirds: []
		}
	}
	useEffect(() => { // åˆæ¬¡é€²å…¥è©²åˆ†é æ™‚ï¼ŒæŠŠä¹‹å‰ç¯©é¸æ­·å²æ¸…é™¤
		setUserParameters();
	}, []);
	// ä½¿ç”¨è€…è¨­å®šæ‰€æœ‰ç¯©é¸åƒæ•¸çš„å€¼
	const handleUserChange = () => {
		let { userDates, userLocations, userBirds } = userParameters;
		let filterVids = [], filterDates = [], filterLocations = [], filterBirds = [];
		for (let i = 0; i < fullVids.length; i++) { // å¾æ‰€æœ‰å½±ç‰‡ä¾†ç¯©é¸
			let fullVid = fullVids[i], fullDate = fullDates[i], fullLocation = fullLocations[i], fullBird = fullBirds[i];
			let fullDateTime = new Date(fullDate).getTime();
			// Date Object æ¯”è¼ƒç”¨ getTime()ï¼›æœ‰æ™‚é–“é»èˆ‡æ™‚é–“å€é–“
			let booleanDate = !userDates.length || (userDates.length === 1 && fullDateTime === new Date(userDates[0]).getTime()) || (userDates.length === 2 && new Date(userDates[1]).getTime() >= fullDateTime && fullDateTime >= new Date(userDates[0]).getTime());
			 // (è‹¥ç„¡è¨­å®šç¯©é¸å‰‡é€šé || è‹¥æœ‰å‰‡ç¯©é¸)
			let booleanLocation = (!userLocations.length || userLocations.includes(fullLocation));
			let booleanBird = (!userBirds.length || userBirds.includes(fullBird));
			if (booleanDate && booleanLocation && booleanBird) {
				filterVids.push(fullVid);
				filterDates.push(fullDate);
				filterLocations.push(fullLocation);
				filterBirds.push(fullBird);
			}
		}
		// ç¶“éç¯©é¸å¾Œå‰©ä¸‹çš„å½±ç‰‡
		let filterVideoCards = vidsToVideoCards(filterVids, filterDates, filterLocations, filterBirds, handleVideoPlay, handleVideoStop);
		setVideoCards(filterVideoCards);
	}
	// ç¯©é¸åƒæ•¸ï¼šæ—¥æœŸ
	const [dateRange, setDateRange] = useState(new Date()); // å–®é¸æ—¥æœŸæˆ–æ—¥æœŸå€é–“
	const handleDateRangeChange = userDateArray => {
		setDateRange(userDateArray); // æ›´æ–°æ—¥æœŸç¯©é¸å€¼(ç‰©ä»¶é™£åˆ—)
		userParameters = { // æ›´æ–°æ—¥æœŸç¯©é¸å€¼(å€¼é™£åˆ—)
			...userParameters,
			userDates: datePickerOutputToValues(userDateArray)
		}
		handleUserChange(); // æ›´æ–°ç¬¦åˆçš„å½±ç‰‡
	}
	// ç¯©é¸åƒæ•¸ï¼šåœ°é»
	const [locations, setLocations] = useState([]); // å¤šé¸åœ°é»æ¸…å–®
	const handleLocationsChange = userLocationObjects => {
		setLocations(userLocationObjects); // æ›´æ–°åœ°é»ç¯©é¸å€¼(ç‰©ä»¶é™£åˆ—)
		userParameters = { // æ›´æ–°åœ°é»ç¯©é¸å€¼(å€¼é™£åˆ—)
			...userParameters,
			userLocations: multiSelectOutputToValues(userLocationObjects)
		}
		handleUserChange(); // æ›´æ–°ç¬¦åˆçš„å½±ç‰‡
	}
	const listLocations = getItems('location'); // ä¸é‡è¤‡åœ°é»
	const locationMultiSelect = itemsToRainbowMultiSelectOptions(listLocations, "ytLocationMultiSelect", "åœ°é»", locations, handleLocationsChange, "è³é³¥åœ°é»"); // åœ°é»ç¯©é¸é¸é …
	// ç¯©é¸åƒæ•¸ï¼šé³¥ç¨®
	const [birds, setBirds] = useState([]); // å¤šé¸é³¥ç¨®æ¸…å–®
	const handleBirdsChange = userBirdObjects => {
		setBirds(userBirdObjects); // æ›´æ–°é³¥ç¨®ç¯©é¸å€¼(ç‰©ä»¶é™£åˆ—)
		userParameters = { // æ›´æ–°é³¥ç¨®ç¯©é¸å€¼(å€¼é™£åˆ—)
			...userParameters,
			userBirds: multiSelectOutputToValues(userBirdObjects)
		}
		handleUserChange(); // æ›´æ–°ç¬¦åˆçš„å½±ç‰‡
	}
	const listBirds = getItems('bird'); // ä¸é‡è¤‡é³¥ç¨®
	const birdMultiSelect = itemsToRainbowMultiSelectOptions(listBirds, "ytBirdMultiSelect", "é³¥ç¨®", birds, handleBirdsChange, "è§€è³é³¥ç¨®"); // é³¥ç¨®ç¯©é¸é¸é …
	// å½±ç‰‡åˆ—è¡¨
	const fullVids = getItems('vid', true); // æ‰€æœ‰é‡è¤‡å½±ç‰‡ ID
	const fullDates = getItems('date', true); // æ‰€æœ‰é‡è¤‡æ—¥æœŸ
	const fullLocations = getItems('location', true); // æ‰€æœ‰é‡è¤‡åœ°é»
	const fullBirds = getItems('bird', true); // æ‰€æœ‰é‡è¤‡é³¥ç¨®
	const fullVideos = vidsToVideoCards(fullVids, fullDates, fullLocations, fullBirds, handleVideoPlay, handleVideoStop); // æ‰€æœ‰é‡è¤‡å½±ç‰‡
	const [videoCards, setVideoCards] = useState(fullVideos);
	// æ»¾å‹•è‡³é ‚æŒ‰éˆ•
	const [showScroll, setShowScroll] = useState(false); // é¡¯ç¤ºç‹€æ…‹
	const handleScrollTop = target => { // æ”¹è®Šé¡¯ç¤ºç‹€æ…‹
		if (!showScroll && target.scrollTop > 500) { // é é¢æ²å‹• 500px å¾Œï¼Œé¡¯ç¤ºæŒ‰éˆ•
			setShowScroll(true);
		} else if (showScroll && target.scrollTop <= 500) { // é é¢æ²å‹•æ¥è¿‘é ‚ç«¯ï¼Œéš±è—æŒ‰éˆ•
			setShowScroll(false);
		}
	}
	// æœ€æ–°æ—¥æœŸä½œç‚ºæ—¥æœŸç¯©é¸ä¸Šé™
	const latestDateYMD = fullDates[0][0].split('-');
	const latestYear = parseInt(latestDateYMD[0]);
	const latestMonth = parseInt(latestDateYMD[1]);
	const latestDay = parseInt(latestDateYMD[2]);

	return (
		<main className="h-100" onScroll={e => {
			handleScrollTop(e.target);
			pauseVideos(playingVideos);
		}}>
			{/* ç¯©é¸åƒæ•¸å€åŸŸ */}
			<div className="sortPanel w-100 d-inline-flex justify-content-evenly pb-3 sticky-top row">
				<div className="col-sm-4 d-inline-flex">
					{/* æ—¥æœŸé¸æ“‡å™¨ */}
					<DatePicker
						id="ytDatePicker"
						formatStyle="medium" // é¡¯ç¤ºæ–¼æ¡†æ ¼ä¸­çš„æ—¥æœŸæ ¼å¼ï¼›small, medium, large
						label="æ—¥æœŸ" // æ¨™é¡Œ
						labelAlignment="left" // æ¨™é¡Œä½ç½®ï¼›left, center, right
						hideLabel={false} // æ¨™é¡Œé¡¯ç¤ºï¼›boolean
						isCentered={true} // æ¡†æ ¼ä¸­çš„æç¤ºä½ç½®ï¼›boolean
						locale="tw" // åœ°å€ï¼›é è¨­ç‚ºç€è¦½å™¨èªè¨€
						maxDate={new Date(latestYear, latestMonth-1, latestDay)} // ä¾ç…§ YouTube è³é³¥ç´€éŒ„æœ€æ–°å½±ç‰‡æ—¥æœŸ
						minDate={new Date(2020, 10, 26)} // ç¬¬ä¸€éƒ¨å½±ç‰‡æ—¥æœŸ
						onChange={value => handleDateRangeChange(value)} // æ›´æ–°æ—¥æœŸç‹€æ…‹
						placeholder="å–®ä¸€æ—¥æœŸ æˆ– æ—¥æœŸå€é–“" // æ¡†æ ¼ä¸­çš„æç¤ºå…§å®¹
						selectionType="range" // æ—¥æœŸæ¨¡å¼ï¼›single, range
						value={dateRange} // æ—¥æœŸçš„å€¼
					/>
					<div id="ytDatePickerDelBtn" onClick={() => {handleDateRangeChange([]);}}>
						<svg xmlns="http://www.w3.org/2000/svg" width="25" height="25" fill="rgba(1,182,245,1)" className="bi bi-x-circle" viewBox="0 0 16 16">
							<path d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14zm0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16z"/>
							<path d="M4.646 4.646a.5.5 0 0 1 .708 0L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 0 1 0-.708z"/>
						</svg>
					</div>
				</div>
				<div className="col-sm-4">
					{locationMultiSelect}
				</div>
				<div className="col-sm-4">
					{birdMultiSelect}
				</div>
			</div>
			{/* å½±ç‰‡å€åŸŸ */}
			<div id="ytVideosContainer">
				{videoCards}
			</div>
			{/* æ»¾å‹•è‡³é ‚æŒ‰éˆ• */}
			<ScrollTopArrow showScroll={showScroll} scrollToElement={() => scrollToElement(document.getElementById("ytVideosContainer"))} />
		</main>
	);
}

export default YouTube;
