import { useState } from 'react';

import { Tabset, Tab } from 'react-rainbow-components';

import EBirdChartsMap from './EBirdChartsMap';

const EBirdCharts = ({ avatarIndex }) => {
	// é¸æ“‡çš„åˆ†é 
	const [selectedTab, setSelectedTab] = useState("map");
	const handleSelectedTabChange = (tabName) => {
		setSelectedTab(tabName);
	}
	// åˆ†é çš„å…§å®¹
	const getTabContent = () => {
		if (selectedTab === "map") { // åœ°åœ–åˆ†é 
			return (<EBirdChartsMap avatarIndex={avatarIndex} />);
		} else if (selectedTab === "timeline") { // æ™‚é–“è»¸åˆ†é 
			return (<div id="timelineTab" aria-labelledby="timeline">timeline</div>);
		}
	}

	return (
		<div id="ebTabsetContainer">
			<Tabset
				id="ebTabset"
				activeTabName={selectedTab} // åˆ†é åå­—ï¼›<Tab name>
				fullWidth={false} // æ˜¯å¦ä¼¸å±•ä½”æ»¿ç©ºé–“ï¼›true, false
				variant="line" // åˆ†é å¤–è§€ï¼›card, line
				onSelect={(e, tabName) => handleSelectedTabChange(tabName)}
			>
				{/* ç•™ä½ç½®çµ¦ eBirder æŠ½å±œé–‹é—œ */}
				<Tab
					id="none"
					label=""
					name="none"
					ariaControls="noneTab"
					disabled={true}
				/>
				<Tab
					id="map" // åˆ†é å…§å®¹çš„ aria-labelledby
					label="åœ°åœ–" // åˆ†é é¡¯ç¤ºåç¨±
					name="map" // çµ¦ <Tabset onSelect> é…å°çš„
					ariaControls="mapTab" // åˆ†é å…§å®¹çš„ id
				/>
				<Tab
					id="timeline"
					label="æ™‚é–“è»¸"
					name="timeline"
					ariaControls="timelineTab"
				/>
			</Tabset>
			{/* åˆ†é å…§å®¹ */}
			{getTabContent()}
		</div>
	);
}

export default EBirdCharts;
