import React, { useState } from 'react';

import YouTube from 'react-youtube';
// å½±ç‰‡å¡ç‰‡å„å…ƒç´ çš„ Class
var classNames = {
	"playButton": "playBtn", // æ¨¡ä»¿çš„æ’­æ”¾æŒ‰éˆ•
	"previewImage": "previewImg", // é è¦½åœ–ç‰‡
	"iframe": "ytVideo" // YouTube å½±ç‰‡å®¹å™¨
}

const YoutubeVideo = ({ videoId, videoLid, handleVideoPlay, handleVideoStop }) => {
	// è¼‰å…¥çœŸæ­£çš„ YouTube å½±ç‰‡
	const [showVideo, setShowVideo] = useState(false);
	const handleShowVideoChange = () => {
		setShowVideo(true);
	}
	/*
	 * YouTube API çš„è¨­å®šé¸é …
	 * https://developers.google.com/youtube/player_parameters
	 */
	const opts = {
		playerVars: {
			autoplay: 1, // 0, 1
			color: "red", // è§€çœ‹é€²åº¦æ¢é¡è‰²ï¼šred, white
			controls: 1, // æ’­æ”¾å™¨æ§åˆ¶é¡¯ç¤ºï¼š0, 1
			list: (videoLid ? videoLid : undefined), // æ’­æ”¾æ¸…å–® ID
			listType: "playlist" // æ’­æ”¾æ¸…å–®é¸å–®
		}
	}
	// é è¨­ç‚ºåœ–ç‰‡ï¼Œé»é¸å¾Œè¼‰å…¥çœŸæ­£å½±ç‰‡
	return !showVideo ?
		React.createElement("div", {
			className: classNames.previewImage,
			style: { // åœ–ç‰‡ç•«è³ªï¼šhqdefault, mqdefault, sddefault, default, maxresdefault
				backgroundImage: "url(https://img.youtube.com/vi/" + videoId + "/hqdefault.jpg)"
			},
			onClick: handleShowVideoChange
		}, React.createElement("div", {
			className: classNames.playButton
		}))
		:
		<YouTube
			videoId={videoId}
			id={videoId}
			className={classNames.iframe} // ç‚ºäº†é™åˆ¶é«˜åº¦
			opts={opts}
			onPlay={e => handleVideoPlay(e.target)}
			onPause={e => handleVideoStop(e.target)}
			onEnd={e => handleVideoStop(e.target)}
		/>
}

export default YoutubeVideo;
